@import "normalizez.scss";
@import "resets.scss";

* {
    box-sizing: border-box; }

@font-face {
    font-family: "MontserratT";
    src: url(`../fonts/MontserratT-Regular.ttf`);
    font-weight: regular;
    font-style: normal; }


@font-face {
    font-family: "MontserratT";
    src: url(`../fonts/MontserratT-SemiBold.ttf`);
    font-weight: bold;
    font-style: normal; }


html {
    font-family: "MontserratT";
    font-weight: regular; }


$color-blue: #5290EA;
$color-orange: #F28B64;

.container {
    position: relative;
    max-width: 1140px;
    margin: 0 auto; }
.main-section {
    position: relative;
    min-height: 800px;
    background: #E5E5E5;
    padding-top: 16px;
    overflow: hidden;

    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center; }
    &__logo {
        width: 134px; }
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 129px;
        right: -8px; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: 650px; } }
.nav-menu {
    &__list {
        display: flex;
        flex-direction: row;
        font-weight: bold; }
    &__list-item {
        margin-right: 64px;
        font-size: 18px;
        &:last-child {
            margin-right: 0px; } }
    a {
        text-decoration: none;
        color: black; } }


.left-wrapper {
    position: relative;
    &__img {
        position: relative;
        z-index: 5; }
    &__rectangle-blue {
        position: absolute;
        top: 150px;
        left: -45px;
        width: 565.95px;
        height: 220px;
        background: $color-blue;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        z-index: 2; }
    &__rectangle-orange {
        position: absolute;
        width: 618.43px;
        height: 220px;
        left: -22px;
        top: 150px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-50.81deg);
        z-index: 3; }
    &__rectangle-grey {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 13px;
        top: 180px;
        z-index: 1;
        background: #e2e2e2;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-36.15deg); } }
.right-wrapper {
        width: 372px;
        position: absolute;
        top: 150px;
        right: -2px;
        z-index: 5;
        &__icons-box {
            margin-bottom: 1px;
            padding-left: 10px; }
        &__title {
                margin-bottom: 5px; }
        &__text {
            font-size: 16px;
            margin-bottom: 20px; } }
.icons {
    display: flex;
    flex-direction: row;
    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid $color-blue;
        border-radius: 100px;
        margin-right: 17px;
        &:last-child {
            margin-right: 0px; }
        i {
            font-size: 21px; } } }
.title {
    font-size: 72px;
    position: relative;
    font-weight: bold;
    &__hr {
        width: 65px;
        position: absolute;
        bottom: 0px;
        margin: 0; } }
.buttons {
    display: inline-flex;
    font-weight: bold;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: none;
        font-size: 18px;
        color: #fff; }
    &__icon-btn {
        display: inline-flex;
        align-items: center;
        img {
            margin-right: 12px; } } }
.elipse {

            width: 194px;
            height: 194px;


            background: linear-gradient(143.92deg, rgba(237, 123, 80, 0.5) 14.85%, rgba(72, 138, 234, 0.5) 53.25%, rgba(44, 44, 44, 0.5) 85.74%);
            border-radius: 150px;
            display: flex;
            justify-content: center;
            align-items: center;
            &_dig {
                width: 282px;
                height: 282px; }
            &__subtract {

                width: 129.33px;
                height: 129.33px;


                background: #E5E5E5;
                border-radius: 150px;
                &_big {
                    width: 188px;
                    height: 188px; } } }

.second-section {
    min-width: 700px;
    margin-bottom: 56px;
    overflow: hidden;
    &__container {
     padding-top: 32px;
     position: relative; }
    &__title {
        margin-bottom: 40px; }
    &__cards-box {
        margin-bottom: 25px;
        display: inline-flex;
        justify-content: center; }
    &__cards-item {
        margin-right: 33px;


        &:last-child {
            margin-right: 0px; } }
    &__icons-box {
        display: flex;
        justify-content: space-evenly;
        font-weight: bold; }
    &__icons-item {

        &:last-child {
            margin-right: 0; } }
    &__first-elipse {
        position: absolute;
        top: 80px;
        right: -68px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        top: 364px;
        left: -68px;
        z-index: -1; } }
.cards-item {
    position: relative;
    width: 360px;
    height: 360px;
    background: #e2e2e2;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;

    &__img {
        height: 332px; }
    &__text {

            width: 280px;
            font-size: 36px;
            text-align: center;
            transform: rotate(90deg);
            position: absolute;
            top: 141px;
            right: -40px;
            font-weight: bold; } }
.cards-item_blue {
    background: $color-blue; }
.cards-item_orange {
    background: $color-orange; }
.icons-item {
    display: inline-flex;
    align-items: center;
    &__img {
        width: 80px;
        margin-right: 20px; }
    &__text {
        font-size: 18px;
        span {
            font-size: 48px;
            font-weight: bold; } } }

.third-section {
    position: relative;
    z-index: 0;
    min-height: 640px;
    background: #F0F0F0;
    overflow: hidden;
    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-left: 167px;
        padding-top: 46px; }
    &__right-wrapper {
        max-width: 480px; }
    &__right-title {
        margin-bottom: 20px; }
    &__text {
        margin-bottom: 30px;
        font-style: 18px; }

    &__first-elipse {
        position: absolute;
        top: 140px;
        left: 110px;
        z-index: -1; }
    &__figure {
        position: absolute;
        top: 249px;
        left: 15px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: -120px; } }

.title__hr_top {
    bottom: 66px; }

.footer {
    &__img {
        position: absolute;
        left: -250px;
        bottom: 0; }
    &__container {
        min-height: 272px;
        display: flex;
        align-items: center;
        justify-content: flex-end; }

    &__title {
        margin-bottom: 20px; } }

@media (max-width: 1199px) {


    .main-section {
        min-height: 690px; }

    .container {
        width: 960px; }

    .left-wrapper {

        &__img {
           width: 750px; }
        &__rectangle-blue {
            width: 500px;
            height: 200px; }
        &__rectangle-orange {
            width: 550px;
            height: 200px; }
        &__rectangle-grey {
            width: 500px;
            height: 200px; } }

    .right-wrapper {
        right: 20px; }

    .main-section__first-elipse {
        right: 10px; }

    .main-section__second-elipse {
        top: 560px; }

    .second-section {
        min-width: 600px;
        &__cards-box {
            display: flex;
            justify-content: center; } }

    .cards-item {
        width: 300px;
        height: 300px;
        margin-right: 20px; }
    .cards-item__img {
        height: 276px; }

    .cards-item__text {
        top: 120px;
        font-size: 30px; }

    .icons-item__img {
        margin-right: 10px; }

    .third-section {
        min-width: 550px; }
    .third-section__container {
        padding-left: 40px; }

    .third-section__right-wrapper {
        max-width: 450px; } }

@media (max-width: 991px) {

    .main-section {
        min-height: 572px; }
    .container {
        width: 720px; }
    .left-wrapper {

        &__img {
            width: 600px; }
        &__rectangle-blue {
            width: 450px;
            height: 180px; }
        &__rectangle-orange {
                width: 450px;
                height: 180px;
                left: -19px;
                top: 167px; }
        &__rectangle-grey {
            width: 450px;
            height: 180px; } }

    .right-wrapper {
        right: -104px;
        top: 81px;
        &__text {
            font-size: 15px;
            width: 264px; }
        &__title {
            font-size: 58px; } }
    .buttons {
        display: inline-flex;
        justify-content: center;

        &__main-btn {
            width: 144px;
            height: 47px;
            font-size: 14px;
            margin-bottom: 14px; }
        &__first-elipse {
            right: -15px;
            top: 99px; } }
    .buttons__main-btn {
        margin-right: 10px; }


    .buttons__icon-btn img {
        margin-right: 12px;
        width: 37px; }

    .elipse {
        width: 170px;
        height: 170px; }
    .elipse__subtract {
        width: 110.33px;
        height: 110.33px; }
    .main-section__first-elipse {
        top: 25px; }
    .main-section__second-elipse {
        top: 521px;
        right: -90px; }

    .second-section {
        margin-bottom: 15px;
        &__title {
            margin-bottom: 32px;
            font-size: 58px; }

        &__first-elipse {
            top: 46px;
            right: -25px; }

        &__second-elipse {
            top: 296px;
            left: -21px;
            z-index: -4; } }
    .cards-item {
        width: 250px;
        height: 250px;
        &__img {
            height: 234px; }
        &__text {
            top: 110px;
            font-size: 27px;
            right: -84px; } }
    .icons-item {
        &__img {
            width: 73px; }
        &__text {
            font-size: 15px; } }

    .third-section {
        min-height: 568px;
        &__left-wrapper {
            margin-right: 32px; }

        &__text {
            width: 362px; }
        &__figure {
            width: 200px; }
        &__second-elipse {
            right: -80px;
            top: -80px; } }
    .title {
        font-size: 58px; }

    .footer {
        &__img {
            width: 599px;
            height: 242px; }
        &__title {
            font-size: 52px; } }

    .title__hr_top {
        bottom: 49px; } }
@media (max-width: 767px) {
    .main-section {
        min-width: 100%;
        min-height: 727px;

        &__first-elipse {
            top: 53px; }

        &__second-elipse {
            top: 648px;
            right: -86px; } }

    .container {
        width: 540px; }


    .nav-menu {

        position: fixed;
        top: 0;
        right: -100%;
        width: 150px;
        height: 100vh;
        background: $color-blue;
        z-index: 30;
        transition: 0.5s all;
        &__list {
          flex-direction: column;
          justify-content: space-evenly;
          align-items: center;
          height: 100%; }
        &__list-item {
            margin-right: 0;
            font-size: 20px; } }

    .hamburger {
        width: 30px;
        position: fixed;
        top: 40px;
        right: 30px;
        height: 14px;
        z-index: 50;
        span {
            transition: 1s all;
            height: 2px;
            width: 100%;
            display: block;
            background: black;
            margin-bottom: 4px;
            &:last-child {
                margin-bottom: 0px; } } }

    .hamburger_active {
        span {
            margin-bottom: -2px;
            &:nth-child(1) {
                transform: translateY(3px) rotate(-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                transform: translateY(3px) rotate(45deg); } } }
    .nav-menu_active {
        right: 0; }

    .left-wrapper {
        &__img {
            width: 100%;
            top: 295px; }
        &__rectangle-blue {

            width: 377px;
            height: 151px;
            top: 404px;
            left: -128px; }
        &__rectangle-orange {

            width: 379px;
            height: 151px;
            left: -85px;
            top: 411px; }
        &__rectangle-grey {
            width: 377px;
            height: 151px;
            left: -51px;
            top: 421px; } }

    .right-wrapper {
        right: -104px;
        top: 81px;
        width: 443px;

        &__text {
            font-size: 15px;
            width: 267px; } }

    .title {
        font-size: 53px; }

    .buttons__main-btn {
        width: 151px;
        height: 47px; }

    .second-section {
        min-width: 100%;
        margin-bottom: 0px;

        &__first-elipse {

            right: 0px; }

        &__second-elipse {
            top: 1199px;
            left: -74px; }

        &__title {
            margin-left: 27px; }

        &__cards-box {
            flex-direction: column;
            margin-left: 96px; }


        .cards-item {

            margin-bottom: 20px;
            margin-right: 0px; }
        .cards-item:last-child {
            margin-bottom: 0px; }

        &__icons-box {
            flex-direction: column;
            margin-left: 101px; }

        .icons-item {
                margin-bottom: 10px; } }

    .third-section {
        min-width: 410px;
        &__container {
            flex-direction: column;
            width: 100%; }

        &__left-wrapper {
            margin-right: 138px; } }

    .footer {
        min-height: 459px;

        &__container {
            flex-direction: column;
            min-height: 200px; }

        &__img {
                top: 211px; } } }

@media (max-width: 575px) {



    .container {
        width: 100%; }

    .second-section {
        &__cards-box {
            margin-left: 70px; }

        &__icons-box {
            margin-left: 81px; } }

    .third-section {
        min-width: 362px;

        &__container {
            padding-left: 0px; }

        &__left-wrapper {
            margin-right: 102px; }

        &__right-wrapper {
            max-width: 305px; }

        &__text {
            width: 308px; } }

    .title {
        font-size: 47px; } }


